body {
  margin: 0;
  padding: 0;
}
.app {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-image: url("./images/12980760_5096154.jpg");
  background-size: cover;
  background-position: center;
}

form {
  background-color: white;
  padding: 0px 60px;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

h1 {
  /* color: rgb(77, 1, 77); */
  color: #561750;
  text-align: center;
}

button {
  width: 100%;
  height: 50px;
  padding: 10px;
  border: none;
  background-color: #561750;
  color: white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 30px;
}
p {
  text-align: center;
  color: rgb(10, 177, 10);
}
